import React, {ChangeEvent} from "react";

import {AddPostAC, PostType, ShowPostTextInTextareaAC} from '../../../redux/profileReducer';
import MyPosts from './MyPosts';
import { RootState} from '../../../redux/storeRedux';
import {connect} from 'react-redux';
import {DispatchType} from '../../../redux/state';
import {getRegisteredUserId, getRegisteredUserLogin, getRegisteredUserPhoto} from '../../util/reduxSelector';


type MyPostsContainerPropsType={
    showTextInTextarea: (e: ChangeEvent<HTMLTextAreaElement>)=>void
    addNewPost:(userId:string, userName:string,userPhoto:string|null,newPost: string,)=>void
    textAreaState: string
    posts: Array<PostType>
    userId:string|null,
    userName:string|null,
    userPhoto:string|null
}
const MyPostsContainer:React.FunctionComponent<MyPostsContainerPropsType>=(props)=>{
    const addPostClosed =(newPost: string,)=>{
        if (props.userId && props.userName){props.addNewPost(props.userId,props.userName,props.userPhoto,newPost)
        }
    }
    return <MyPosts addNewPost={addPostClosed} posts={props.posts} showTextInTextarea={props.showTextInTextarea} textAreaState={props.textAreaState}/>
}

type mapStateToPropsReturnType = {
    textAreaState: string
    posts: Array<PostType>
}
type mapDispatchToPropsReturnType = {
    showTextInTextarea: (e: ChangeEvent<HTMLTextAreaElement>)=>void
    addNewPost:(userId:string, userName:string,userPhoto:string|null,newPost: string,)=>void
}

const mapStateToProps = (state:RootState) => ({
    textAreaState: state.profilePage.textAreaForUserStatus,
    posts:state.profilePage.posts,
    userId:getRegisteredUserId(state),
    userName:getRegisteredUserLogin(state),
    userPhoto:getRegisteredUserPhoto(state),
})
const mapDispatchToProps = (dispatch:DispatchType):mapDispatchToPropsReturnType => ({
    addNewPost: (userId:string, userName:string,userPhoto:string|null,newPost: string,) => dispatch(AddPostAC(userId,userName,userPhoto,newPost,)),
    showTextInTextarea: (e: ChangeEvent<HTMLTextAreaElement>) => dispatch(ShowPostTextInTextareaAC(e.currentTarget.value))
})


export default connect(mapStateToProps,mapDispatchToProps)(MyPostsContainer);
