import {ActionsTypes} from './state';
import {v1} from 'uuid';
import {Dispatch} from 'redux';
import {UsersAPI} from '../api/socialNetworkAPI';

// Types

// Types of type for action creators
enum actions {
    followUser = 'cirkle/UserReducer/FOLLOW_USER',
    unfollowUser = 'cirkle/UserReducer/UNFOLLOW_USER',
    setUsers = 'cirkle/UserReducer/SET_USERS',
    setUsersTotalCount = 'cirkle/UserReducer/SET_USERS_TOTAL_COUNT',
    setCurrentPage = 'cirkle/UserReducer/SET_USERS_CURRENT_PAGE',
    isFetchingUsersList = 'cirkle/UserReducer/IS_FETCHING_USERS_LIST',
    toggleFollowingInProgress = 'cirkle/UserReducer/TOGGLE_FOLLOWING_IN_PROGRESS',
}

// Type of user state
export type UsersPagePropsType = typeof usersInitialState

// Type of user location
type UsersLocationType = {
    city: string
    country: string
}

// Type of user
export type UsersType = {
    followed: boolean
    id: string
    name: string
    photos: {
        large: string | null
        small: string | null
    }
    status: string | null
    uniqueUrlName: string | null
}



// Initial state
const usersInitialState = {
    users: [] as Array<UsersType>,
    totalCount: 0,
    pageSize: 9,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [] as Array<string>,
}


//Action Creators

// Action creator for subscribe to user
export const follow_userSUCCESS = (userID: string | number) => {
    return {
        type: actions.followUser,
        payload: {
            userID,
        }
    } as const
};

// Action creator for unfollow user
export const unfollow_userSUCCESS = (userID: string | number) => ({
    type: actions.unfollowUser,
    payload: {
        userID,
    }
} as const);

// Action creator for get list of users
export const set_users = (users: Array<UsersType>) => ({
    type: actions.setUsers,
    payload: {
        users,
    }
} as const)

// Action creator for know total count of users
export const set_users_total_count = (totalCount: number) => ({
    type: actions.setUsersTotalCount,
    payload: {
        totalCount,
    }
} as const)

// Action creator for set current page in list of users
export const set_users_currentPageSUCCESS = (currentPage: number) => ({
    type: actions.setCurrentPage,
    payload: {
        currentPage,
    }
} as const)

// Action creator for set preloader
export const toggle_isFetching = (isFetching: boolean) => ({
    type: actions.isFetchingUsersList,
    payload: {
        isFetching,
    }
} as const)

export const toggle_followingInProgress = (isFetching: boolean, userId: string) => ({
    type: actions.toggleFollowingInProgress,
    payload: {
        isFetching,
        userId,
    }
} as const)


// Reducer
export const usersReducer = (state = usersInitialState, action: ActionsTypes): UsersPagePropsType => {
    switch (action.type) {
        case actions.followUser: {
            return {
                ...state,
                users: state.users.map((u) => {
                    if (u.id === action.payload.userID) {
                        return {
                            ...u,
                            followed: false
                        }
                    }
                    return u
                })
            }
        }
        case actions.unfollowUser: {

            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.payload.userID) {
                        return {
                            ...u,
                            followed: true
                        }
                    }
                    return u
                })
            }
        }
        case actions.setUsers : {
            return {
                ...state,
                users: action.payload.users
            }
        }
        case actions.setUsersTotalCount: {
            return {
                ...state,
                totalCount: action.payload.totalCount
            }
        }
        case actions.setCurrentPage: {
            return {
                ...state,
                currentPage: action.payload.currentPage
            }
        }
        case actions.isFetchingUsersList: {
            return {
                ...state,
                isFetching: action.payload.isFetching
            }
        }
        case actions.toggleFollowingInProgress: {

            let stateCopy = {
                ...state,
                followingInProgress: action.payload.isFetching ? [...state.followingInProgress, action.payload.userId] : state.followingInProgress.filter(id => id != action.payload.userId)
            }
            return stateCopy
        }


    }
    return state;
}



// Thunk Creators

// thunk creator to get all number of users
export const setUpAllUsers = (pageSize: number, currentPage: number) => async (dispatch: Dispatch) => {
    dispatch(toggle_isFetching(true))
    let data = await  UsersAPI.getUsers(pageSize, currentPage)
        dispatch(set_users_total_count(data.totalCount))
        dispatch(set_users(data.items))
        dispatch(toggle_isFetching(false))
}


// thunk creator to set up current page of users
export const setUpCurrentPage = (pageSize: number, currentPage: number, pageNumber: number) => async (dispatch: Dispatch) => {
    dispatch(toggle_isFetching(true))
    dispatch(set_users_currentPageSUCCESS(pageNumber))
    let data = await UsersAPI.getUsers(pageSize, currentPage)
        dispatch(set_users_total_count(data.totalCount))
        dispatch(set_users(data.items))
        dispatch(toggle_isFetching(false))
}

// thunk creator to follow user
export const followUser = (userId: string) => async (dispatch: Dispatch) => {
    dispatch(toggle_followingInProgress(true, userId,))
    let data = await  UsersAPI.foolowUser(userId, {})
        if (data.resultCode == 0) {
            dispatch(unfollow_userSUCCESS(userId))
            dispatch(toggle_followingInProgress(false, userId,))
        }
};

// thunk creator to unfollow user
export const unfollowUser = (userId: string) =>  async (dispatch: Dispatch) => {
    dispatch(toggle_followingInProgress(true, userId,))
    let data = await  UsersAPI.unfollowUser(userId)
        if (data.resultCode == 0) {
            dispatch(follow_userSUCCESS(userId))
            dispatch(toggle_followingInProgress(false, userId,))
        }
}